import React from "react";
import { withStyles } from "@material-ui/core/styles";
//import GenericApp from "@iobroker/adapter-react/GenericApp";
import ConfigItem from "./ConfigItem";
import { styles, AddTooltip, IButton, TButton, ScrollTop, LoadButton } from "./UiComponents";
//import ChipInput from "material-ui-chip-input";
import { Iob, t, logSnackbar } from "./Iob";
import {
  Avatar,
  AppBar,
  Toolbar,
  Typography,
  Container,
  Paper,
  Icon,
  IconButton,
  Tabs,
  Tab,
  Grid,
  Divider,
} from "@material-ui/core";

import configtool from "../../assets/config.json";
//import { config } from "chai";
//import { isNoSubstitutionTemplateLiteral } from "typescript";
//import { isNotEmittedStatement } from "typescript";

class ConfigSettings extends React.Component {
  constructor(props) {
    super(props);
    this.classes = props.classes;
    //    const nconf = ConfigSettings.transformConfig(props.configPage);
    this.state = {
      tab: 0,
      config: [[]],
      page: [{ no_page: "No Page" }],
    };
  }

  static getDerivedStateFromProps(props, state) {
    let newState = null;
    //    console.log("derivedState:", state.opvalue, props.value, state.value);
    if (props.configPage !== state.configPage) {
      //      console.log("Prop value changed!", state.opvalue, props.value, state.value);
      const config = ConfigSettings.transformConfig(props.configPage);
      const tab = state.tab || 0;
      newState = {
        config,
        configPage: props.configPage,
        page: config[tab] || [],
      };
    }
    return newState;
  }

  static transformConfig(config) {
    function translateConfig(arr) {
      const ret = arr.map((item) => {
        const { items, ...other } = item;
        const tnew = {};
        const res = Object.keys(other).map(
          (name) =>
            (tnew[name] =
              ["label", "tooltip", "placeholder", "hint", "text", "html"].indexOf(name) >= 0 && typeof other[name] === "string"
                ? t(other[name])
                : other[name])
        );
        if (items) tnew.items = translateConfig(items);
        return tnew;
      });
      return ret;
    }
    //    console.log("config:", config);
    const conf = (config && config.configTool) || [];
    const res = translateConfig(conf).map((p) => {
      let { spacing, ...other } = p;
      if (!spacing) other.spacing = 2;
      return { ...other };
    });
    return res ? res : [];
  }

  handleTabChange(e, t) {
    //    console.log(e, t);
    this.setState({ tab: t, page: this.state.config[t] });
  }

  renderToolbarAdapter() {
    const { instanceConfig, displayLanguage, adapterInstance, adapterStatus } = this.props;
    let readme = (instanceConfig && instanceConfig.common && instanceConfig.common.readme) || "#";
    const lang = displayLanguage;
    if (lang != "en")
      readme = `https://translate.google.com/translate?sl=auto&tl=${lang}&u=${encodeURIComponent(
        readme
      )}`;

    return (
      <React.Fragment>
        <Paper elevation={0} variant="outlined">
          <Avatar src={"./" + instanceConfig.common.icon} variant="square" />
        </Paper>
        <Typography variant="h6" color="inherit">
          &nbsp;&nbsp;{adapterInstance}&nbsp;
        </Typography>
        <Typography variant="subtitle2" color="inherit">
          &nbsp;v{instanceConfig.common.version}&nbsp;&nbsp;
        </Typography>
        {AddTooltip(
          t("Open Readme"),
          <IconButton
            edge="start"
            className={this.classes.menuButton}
            color="inherit"
            aria-label="menu"
            href={readme}
            target="_"
          >
            <Icon color="inherit">help_outline</Icon>
          </IconButton>
        )}
        <IButton
          tooltip={!adapterStatus.status ? t("start adapter") : t("stop adapter")}
          style={{
            margin: "0px 10px",
            color: !adapterStatus.alive ? "red" : adapterStatus.connected ? "green" : "orange",
          }}
          onClick={(e) => Iob.enableDisableAdapter(!adapterStatus.alive)}
          icon={!adapterStatus.status ? "play_circle" : "pause_circle"}
          size="large"
        />
        <IButton
          disabled={!adapterStatus.alive}
          tooltip={t("restart adapter")}
          style={{
            margin: "0px 10px",
          }}
          onClick={(e) => Iob.setLoglevel()}
          icon={"replay"}
        />
      </React.Fragment>
    );
  }

  renderConfigSave() {
    const {
      inative,
      adapterInstance,
      displayLanguage,
      narrowWidth,
      inativeChanged,
      classes,
    } = this.props;
    return (
      <React.Fragment>
        <TButton
          tooltip={t("backupConfig")}
          endIcon="save_alt"
          color="inherit"
          size="large"
          onClick={(e) =>
            Iob.saveFile(
              { native: inative },
              {
                stringify: true,
                name:
                  adapterInstance +
                  "_config_" +
                  new Date()
                    .toLocaleDateString(displayLanguage, {
                      year: "numeric",
                      month: "numeric",
                      day: "numeric",
                      hour: "2-digit",
                      minute: "2-digit",
                      second: "2-digit",
                    })
                    .split(", ")
                    .join("-"),
              },
              e
            )
          }
          narrow={narrowWidth}
          //          label={t("ra_Save Config")}
        />
        <LoadButton
          tooltip={t("restoreConfig")}
          endIcon="system_update_alt"
          color="inherit"
          size="large"
          dropStyle={{ color: "darkred" }}
          dropLabel={t("drop single file")}
          receivedFile={(value, file) => {
            //            Iob.logSnackbar("info;file loaded: {0}", file.path);
            let native = null;
            debugger;
            try {
              native = JSON.parse(value);
            } catch (e) {
              Iob - logSnackbar("error;cannotParseNative", e);
            }
            if (native && native.native) {
              Iob.setStore.setInative(native.native);
              Iob.logSnackbar("success;config file loaded: {0}", file);
            } else Iob.logSnackbar("error;cannotParseNative", file);
          }}
          narrow={narrowWidth}
        />
        <TButton
          tooltip={t("rt_Saveconfig")}
          disabled={!inativeChanged}
          endIcon="save"
          size="large"
          color="inherit"
          onClick={() => Iob.configSave(false)}
          narrow={narrowWidth}
          label={t("ra_Save")}
        />
        <TButton
          tooltip={t("ttSaveClose")}
          disabled={!inativeChanged}
          endIcon="settings_power"
          size="large"
          color="inherit"
          onClick={() => Iob.configSave(true)}
          narrow={narrowWidth}
          label={t("raSaveclose")}
        />
        <TButton
          tooltip={t("Cancel & Close")}
          color="inherit"
          size="large"
          className={classes.menuButton}
          endIcon="close"
          onClick={() => Iob.onClose()}
          narrow={narrowWidth}
          label={t("ra_cancel")}
        />
      </React.Fragment>
    );
  }

  render() {
    const isHideItem = (hideItem, key) => {
      if (typeof hideItem === "string")
        try {
          const fun = Iob.makeFunction(hideItem, this, "props", "Iob");
          const res = fun(this.props, Iob);
//          console.log("hideItem", key, hideItem, res);
          if (res) return true;
        } catch (e) {
          Iob.logSnackbar("error; error in displayIf for "+key + ":"+ e);
        }
      else if (typeof hideItem === "boolean" && hideItem) return true;
      return false;
    };
    const { tab, page, config } = this.state;
    if (!page || !page.items || !page.items.length) return null;
    const { inative } = this.props;
    return (
      //      <div className={this.props.classes.tab}>
      <React.Fragment>
        <AppBar position="sticky" style={{ position: "fixed !important" }}>
          <Toolbar variant="dense">
            {this.renderToolbarAdapter()}
            <div style={{ flexGrow: 1 }} />
            <Tabs
              value={tab}
              onChange={(e, t) => this.handleTabChange(e, t)}
              textColor="inherit"
              centered
            >
              {config.map((p, index) => {
                const { icon, label, hideItem, tooltip } = p;
                const key = `tab${index}`;
                if (hideItem && isHideItem(hideItem, key))
                  return null;
                const tab = (
                  <Tab
                    key={key}
                    icon={icon ? <Icon>{icon}</Icon> : null}
                    label={label}
                    value={index}
                  />
                );
                return AddTooltip(tooltip, tab, key);
              })}
            </Tabs>
            <div style={{ flexGrow: 1 }} />
            {this.renderConfigSave()}
          </Toolbar>
        </AppBar>
        <ScrollTop />

        <Container maxWidth={false} disableGutters style={{ overflow: "hidden" }}>
          <Paper elevation={2} style={{ padding: "4px 4px", margin: "3px 3px" }}>
            <Grid
              container
              spacing={page.spacing || 2}
              style={{ paddingTop: "4px", paddingBottom: "16px" }}
            >
              {page.items.map((item, index) => {
                const {
                  cols,
                  noGrid,
                  hideItem,
                  xs,
                  xl,
                  sm = cols ? cols : 2,
                  md,
                  lg,
                  vdivider,
                  ...items
                } = item;
                const split = { xs, xl, sm, md, lg };
                const rest = { ...split, ...items };
                const key = `${tab}/${index}/${items.itype}`;
                if (hideItem && isHideItem(hideItem, key)) return null;
/*                 if (typeof hideItem === "string")
                  try {
                    const fun = Iob.makeFunction(hideItem, this, "props", "Iob");
                    const res = fun(this.props, Iob);
                    if (res) return null;
                  } catch (e) {
                    Iob.logSnackbar("error; error in displayIf for {0}: {1}", key, e);
                  }
                else if (typeof hideItem === "boolean" && hideItem) return null;
 */                let configItem;
                if (rest.itype == "$divider")
                  return (
                    <Grid item sm={12} key={key + "d"}>
                      <Divider variant="fullWidth" />
                    </Grid>
                  );
                configItem = (
                  <ConfigItem
                    key={key + "C"}
                    item={items}
                    index={key}
                    inative={inative}
                    attr={item.field || "$undefined"}
                    field={item.field}
                    value={inative[item.field]}
                    settings={this}
                    itype={items.itype}
                  />
                );
                return [
                  vdivider == "start" ? (
                    <Divider key={key + "v"} orientation="vertical" flexItem></Divider>
                  ) : null,
                  noGrid ? (
                    configItem
                  ) : (
                    <Grid item {...split} key={key}>
                      {configItem}
                    </Grid>
                  ),
                  vdivider == "end" ? (
                    <Divider key={key + "v"} orientation="vertical" flexItem></Divider>
                  ) : null,
                ];
              })}
            </Grid>
          </Paper>
        </Container>
      </React.Fragment>
    );
  }
}

export default withStyles(styles)(
  Iob.connect((state) => {
    const { ...all } = state;
    return { ...all };
  })(ConfigSettings)
);
